$green: #00a94f;

.ui-helper-hidden {
	display: none;
}
.ui-helper-hidden-accessible {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}
.ui-helper-reset {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	line-height: 1.3;
	text-decoration: none;
	font-size: 100%;
	list-style: none;
}
.ui-helper-clearfix {
	&:before {
		content: "";
		display: table;
		border-collapse: collapse;
	}
	&:after {
		content: "";
		display: table;
		border-collapse: collapse;
		clear: both;
	}
}
.ui-helper-zfix {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	position: absolute;
	opacity: 0;
	filter: Alpha(Opacity=0);
}
.ui-front {
	z-index: 100;
}
.ui-state-disabled {
	cursor: default !important;
	pointer-events: none;
	opacity: .35;
	filter: Alpha(Opacity=35);
	background-image: none;
	.ui-icon {
		filter: Alpha(Opacity=35);
	}
}
.ui-icon {
	display: inline-block;
	vertical-align: middle;
	margin-top: -.25em;
	position: relative;
	text-indent: -99999px;
	overflow: hidden;
	background-repeat: no-repeat;
	width: 16px;
	height: 16px;
	background-image: url("/img/upload/datepicker/ui-icons_444444_256x240.png");
}
.ui-widget-icon-block {
	left: 50%;
	margin-left: -8px;
	display: block;
}
.ui-widget-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #aaaaaa;
	opacity: .3;
	filter: Alpha(Opacity=30);
}
.ui-datepicker {
	width: 320px;
	padding: .2em .2em 0;
	display: none;
	.ui-datepicker-header {
		position: relative;
		padding: .2em 0;
	}
	.ui-datepicker-prev {
		position: absolute;
        width: 10px;
        height: 10px;
        left:15px;
        top: 0;
        bottom: 0;
        padding:0;
        margin: auto;
		span {
			position:absolute;
            content:"";
            display: block;
            margin: 30px auto;
            width: 10px;
            height: 10px;
            border-top: 2px solid #666;
            border-left: 2px solid #666;
            transform: rotate(-45deg);
            background-image:none !important;
            left:auto;
            right:auto;
            top: 0;
            bottom: 0;
            padding:0;
            margin: auto;
		}
	}
	.ui-datepicker-next {
		position: absolute;
        width: 10px;
        height: 10px;
        right:15px;
        top: 0;
        bottom: 0;
        padding:0;
        margin: auto;
		span {
			position:absolute;
            content:"";
            display: block;
            margin: 30px auto;
            width: 10px;
            height: 10px;
            border-top: 2px solid #666;
            border-left: 2px solid #666;
            transform: rotate(135deg);
            background-image:none !important;
            left:auto;
            right:auto;
            top: 0;
            bottom: 0;
            padding:0;
            margin: auto;
		}
	}
	.ui-datepicker-prev-hover {
        &.ui-state-hover{
            background:transparent;
            border:0;
        }
        span{
            background-image:none !important;
            border-top: 2px solid $green;
            border-left: 2px solid $green;
        }
		// top: 1px;
		// left: 1px;
	}
	.ui-datepicker-next-hover {
		// top: 1px;
		// right: 1px;
        &.ui-state-hover{
            background:transparent;
            border:0;
        }
        span{
            background-image:none !important;
            border-top: 2px solid $green;
            border-left: 2px solid $green;
        }
	}
	.ui-datepicker-title {
		margin: 0 2.3em;
		line-height: 1.8em;
		text-align: center;
		select {
			// font-size: 1em;
			// margin: 4px 0;
            // background-color:#efefef;
		}
	}
	select.ui-datepicker-month {
		width: 100px;
        // border:1px solid #eee;
	}
	select.ui-datepicker-year {
		width: 100px;
        // border:1px solid #eee;
	}
	table {
		width: 100%;
		font-size: .9em;
		border-collapse: collapse;
		margin: 0 0 .4em;
	}
	th {
		padding: .7em .3em;
		text-align: center;
		font-weight: bold;
		border: 0;
	}
	td {
		border: 0;
		padding: 1px;
		span {
			display: block;
			padding: .2em;
			text-align: right;
			text-decoration: none;
		}
		a {
			display: block;
			padding: .2em;
			text-align: right;
			text-decoration: none;
		}
	}
	.ui-datepicker-buttonpane {
		background-image: none;
		margin: .7em 0 0 0;
		padding: 0 .2em;
		border-left: 0;
		border-right: 0;
		border-bottom: 0;
		button {
			float: right;
			margin: .5em .2em .4em;
			cursor: pointer;
			padding: .2em .6em .3em .6em;
			width: auto;
			overflow: visible;
		}
		button.ui-datepicker-current {
			float: left;
		}
	}
	.ui-icon {
		display: block;
		text-indent: -99999px;
		overflow: hidden;
		background-repeat: no-repeat;
		left: .5em;
		top: .3em;
	}
}
.ui-datepicker.ui-datepicker-multi {
	width: auto;
}
.ui-datepicker-multi {
	.ui-datepicker-group {
		float: left;
		table {
			width: 95%;
			margin: 0 auto .4em;
		}
	}
	.ui-datepicker-group-last {
		.ui-datepicker-header {
			border-left-width: 0;
		}
	}
	.ui-datepicker-group-middle {
		.ui-datepicker-header {
			border-left-width: 0;
		}
	}
	.ui-datepicker-buttonpane {
		clear: left;
	}
}
.ui-datepicker-multi-2 {
	.ui-datepicker-group {
		width: 50%;
	}
}
.ui-datepicker-multi-3 {
	.ui-datepicker-group {
		width: 33.3%;
	}
}
.ui-datepicker-multi-4 {
	.ui-datepicker-group {
		width: 25%;
	}
}
.ui-datepicker-row-break {
	clear: both;
	width: 100%;
	font-size: 0;
}
.ui-datepicker-rtl {
	direction: rtl;
	.ui-datepicker-prev {
		right: 2px;
		left: auto;
		&:hover {
			right: 1px;
			left: auto;
		}
	}
	.ui-datepicker-next {
		left: 2px;
		right: auto;
		&:hover {
			left: 1px;
			right: auto;
		}
	}
	.ui-datepicker-buttonpane {
		clear: right;
		button {
			float: left;
		}
		button.ui-datepicker-current {
			float: right;
		}
	}
	.ui-datepicker-group {
		float: right;
	}
	.ui-datepicker-group-last {
		.ui-datepicker-header {
			border-right-width: 0;
			border-left-width: 1px;
		}
	}
	.ui-datepicker-group-middle {
		.ui-datepicker-header {
			border-right-width: 0;
			border-left-width: 1px;
		}
	}
}
.ui-slider {
	position: relative;
	text-align: left;
	.ui-slider-handle {
		position: absolute;
		z-index: 2;
		width: 1.2em;
		height: 1.2em;
		cursor: default;
		-ms-touch-action: none;
		touch-action: none;
	}
	.ui-slider-range {
		position: absolute;
		z-index: 1;
		font-size: .7em;
		display: block;
		border: 0;
		background-position: 0 0;
	}
}
.ui-slider.ui-state-disabled {
	.ui-slider-handle {
		filter: inherit;
	}
	.ui-slider-range {
		filter: inherit;
	}
}
.ui-slider-horizontal {
	height: .8em;
	.ui-slider-handle {
		top: -.3em;
		margin-left: -.6em;
	}
	.ui-slider-range {
		top: 0;
		height: 100%;
	}
	.ui-slider-range-min {
		left: 0;
	}
	.ui-slider-range-max {
		right: 0;
	}
}
.ui-slider-vertical {
	width: .8em;
	height: 100px;
	.ui-slider-handle {
		left: -.3em;
		margin-left: 0;
		margin-bottom: -.6em;
	}
	.ui-slider-range {
		left: 0;
		width: 100%;
	}
	.ui-slider-range-min {
		bottom: 0;
	}
	.ui-slider-range-max {
		top: 0;
	}
}
